<template>
    <div class="container">
        <div v-if="orgInfo.name && scores.READ_ONLY_API_TOKEN" class="row">
            <div class="col-md-10 mx-auto">
                <b-jumbotron bg-variant="purple" text-variant="white" v-if="orgScore >= 50" class="text-centered">
                    <template slot="header">{{tier1Prize}}!</template>
                    <h2>{{orgInfo.name}}<br/>Completed {{orgScore/10}} of 5 Steps</h2>
                </b-jumbotron>
                <b-jumbotron bg-variant="pink"text-variant="white" v-if="orgScore < 50" class="text-centered">
                    <template slot="header">So Close!</template>
                    <h2>You need to complete all 5 steps in order to get a {{tier1Prize}}</h2>
                    <h2>Go back to <a :href="appUrl">{{appUrl}}</a> and finish the steps</h2>
                    <br/>
                    <h2>{{orgInfo.name}}<br/>Completed {{orgScore/10}} of 5 Steps</h2>
                </b-jumbotron>
            </div>
        </div>
        <div v-if="orgInfo.name && !scores.READ_ONLY_API_TOKEN" class="row">
            <div class="col-md-10 mx-auto">
                <b-jumbotron bg-variant="pink" text-variant="white" class="text-centered">
                    <template slot="header">No Information Found</template>
                    <h2>{{orgInfo.name}}</h2>
                </b-jumbotron>
            </div>
        </div>
        <div v-if="!orgInfo.name" class="row">
            <div class="col-md-10 mx-auto">
                <img src="../assets/ahahah.gif"/>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'

    import appConfig from '../.app.config'
    import orgScoreStore from '../model/orgScoreStore'

    export default {
        name: 'hello',
        data() {
            return {
                name: appConfig.event.name,
                busy: false,
                busyMsg: '',
                scores: orgScoreStore.state.scores,
                orgInfo: orgScoreStore.state.org,
                appBaseDomain: appConfig.appBaseDomain,
                tier1Prize: appConfig.tier1Prize
            }
        },
        computed: {
          orgScore: function () {
              if (!this.scores.READ_ONLY_API_TOKEN) { return undefined }
              var score = 0
              Object.values(this.scores).forEach(value => score += value)
              return score
          },
          appUrl: function () {
            return 'https://' +
                this.orgInfo.name.substring(0, this.orgInfo.name.indexOf('-')) + '.' + this.appBaseDomain
          }
        },
        mounted: function () {
            // if (this.$route.params.org) {
            //     console.log(this.$route.params.org)
            //     orgScoreStore.setOrgName(this.$route.params.org)
            //     orgScoreStore.loadScores()
            // }
        }
    }

</script>

